import React, { FunctionComponent } from 'react';
import styled from '@emotion/styled';
import GlobalStyle from 'components/Common/GlobalStyle';
import Introduction from 'components/Main/Introduction';
import Footer from 'components/Common/Footer';
import CategoryList from 'components/Main/CategoryList';
import PostList, { PostType } from 'components/Main/PostList';
import { graphql } from 'gatsby';
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image'; // Import GatsbyImage

import { PostListItemType } from 'types/PostItem.types';

type IndexPageProps = {
  data: {
    allMarkdownRemark: {
      edges: PostListItemType[];
    };
    file: {
      childImageSharp: {
        gatsbyImageData: IGatsbyImageData;
      } | null; // Handle null value for childImageSharp
    };
  };
};

const CATEGORY_LIST = {
  ALL: 0,
  Web: 2,
  Mobile: 0,
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
`

const IndexPage: FunctionComponent<IndexPageProps> = function ({
  data: {
    allMarkdownRemark: { edges },
    file,
  },
}) {
   const gatsbyImageData = file?.childImageSharp?.gatsbyImageData;// Check if childImageSharp exists
   return (
    <Container>
      <GlobalStyle />
      <Introduction profileImage={gatsbyImageData} />
      <CategoryList selectedCategory="Web" CategoryList={CATEGORY_LIST} />
      <PostList posts={edges} />
      <Footer />
    </Container>
   );
};

export default IndexPage

export const query = graphql`
  query getPostList {
    allMarkdownRemark(
      sort: [{frontmatter: {date: DESC}}, {frontmatter: {title: ASC}}]
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            summary
            date(formatString: "YYYY.MM.DD.")
            categories
            thumbnail {
              childImageSharp {
                gatsbyImageData(width: 768, height: 400)
              }
            }
          }
        }
      }
    }
    file(name: {eq: "profile-image"}) {
      childImageSharp {
        gatsbyImageData(width: 120, height: 120)
      }
    }
  }
`